<template>
  <select name="select-league" id="select-league" :value="value" @input="$emit('input', $event.target.value)">
    <option v-for="(league,key) in leagues" 
            :key="key" 
            :value="league.id">
            {{league.name}}
            {{"(" + league.country + ")"}} 
            {{"-" + league.type}}
    </option>
  </select> 
</template>

<script>
export default {
 props:[
     "leagues","value"
 ]
}
</script>

