import Vue from 'vue'
import App from './App.vue'
import LeagueInfo from './components/LeagueInfo.vue'
import TableHead from './components/TableHead.vue'
import TableBody from './components/TableBody.vue'
import SelectLeague from './components/SelectLeague.vue'
import SelectSeason from './components/SelectSeason.vue'

Vue.component('app-LeagueInfo',LeagueInfo)
Vue.component('app-TableHead',TableHead)
Vue.component('app-TableBody',TableBody)
Vue.component('app-SelectLeague',SelectLeague)
Vue.component('app-SelectSeason',SelectSeason)

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
}).$mount('#app')

