  methods: {
    fetchItems(){
      // "https://v3.football.api-sports.io/leagues"
      fetch(`https://v3.football.api-sports.io/standings?league=${this.leagueSelect}&season=${this.seasonSelect}`, {
        "method": "GET",
        "headers": {
            "x-rapidapi-host": this.Api,
            "x-rapidapi-key": this.ApiKey
        }
      })
      .then(response => response.json())
      .then(response => {
          console.log(response);
          this.items = response.response;
          this.standings = this.items[0].league.standings[0]
          console.log(this.standings);
          this.isFetched = true;
      })
      .catch(err => {
          console.log(err);
      });
    },
    seasonsWatch(){
    if (this.seasonWatch == this.seasonSelect) {
      this.seasonWatch++;
    }else{
      this.seasonWatch = this.seasonSelect;
      this.seasonWatch++;
    }
